  .EnTete{
    background-color: #aa804a;
    height:4vw;
    width: 100vw;
    display:flex;
    flex-direction:row;
  }

  .Titre{
    height:4vw;
    width: 100vw;
    display:flex;
    flex-direction:row;
    justify-content: center;
    }

    .Drapeau{
      height: 3.5vw;
      width: auto;
      align-self: center;
    }

  .Texte{
    align-self: center;
    margin-right:1vw;
    font-weight: bolder;
    font-size: 3vw;
  }

  .LogoNavMenu{
    height: 2vw;
    margin: 1vw;
    transition: scale 0.3s ease;
  }
  
  .LogoNavMenu:hover{
    scale: 1.2;
  }
  
  .NavMenu{
    display:flex;
    flex-direction: column;
    align-items: center;
    background-color: #aa804a;
    width: 30vw;
    border-bottom-right-radius: 3vw;
    position: absolute;
    z-index:1;
    padding-bottom: 2vw;
  }

  .NavMenuBouton{
    margin:2px;
    color:black;
    background-color: #aa804a;
    border-radius: 5px;
    width: 15vw;
    border-style: hidden;
    font-size: 1.5vw;
  }

  .NavMenuBouton:hover{
    color: #DEB887;
    text-shadow: #aa804a;
    font-weight: bolder;
  }
